body {
  margin: 0; }

.container {
  margin: 0 auto;
  max-width: 1000px; }

.header {
  margin: 0 auto; }

.header__top {
  max-width: 940px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  background: #ffffff; }

.header__bottom {
  padding-left: 25px;
  padding-right: 25px;
  background: #1f1f1f; }

.header__bottom-center {
  max-width: 950px;
  margin: 0 auto;
  display: flex;
  padding-top: 10px; }

.socials {
  margin-right: 12%; }

.socials__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: 130px; }

.socials__item {
  width: 32px;
  height: 31px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #c2c2c2;
  border-bottom: 1px solid #c2c2c2;
  border-left: 1px solid #c2c2c2; }

.socials__item:last-of-type {
  border-right: 1px solid #c2c2c2; }

.socials__link {
  font-size: 0;
  width: 100%;
  height: 100%; }

.socials__link:hover {
  opacity: 0.7; }

.socials__link:focus {
  opacity: 0.7; }

.socials__link--fb {
  background: url("../img/icon-facebook.png") 50% 50% no-repeat; }

.socials__link--vk {
  background: url("../img/icon-vk.png") 50% 50% no-repeat; }

.socials__link--tw {
  background: url("../img/icon-twitter.png") 50% 50% no-repeat; }

.socials__link--g {
  background: url("../img/icon-google.png") 50% 50% no-repeat; }

.socials--footer {
  margin-top: 32px; }

.socials__item--footer {
  border-top: 1px solid #b98348;
  border-bottom: 1px solid #b98348;
  border-left: 1px solid #b98348; }

.socials__item--footer:last-of-type {
  border-right: 1px solid #b98348; }

.socials__link--footer--fb {
  background: url("../img/icon-facebook-footer.png") 50% 50% no-repeat; }

.socials__link--footer--vk {
  background: url("../img/icon-vk-footer.png") 50% 50% no-repeat; }

.socials__link--footer--tw {
  background: url("../img/icon-twitter-footer.png") 50% 50% no-repeat; }

.socials__link--footer--g {
  background: url("../img/icon-google-footer.png") 50% 50% no-repeat; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.city {
  display: flex;
  align-items: center;
  padding-left: 20px;
  margin-top: 8px;
  margin-right: 13%; }

.city__name {
  font-family: "Proxima Nova Condensed", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 600;
  margin-right: 12px;
  position: relative;
  color: #1f1f1f;
  cursor: pointer;
  border-bottom: 1px solid #c7c7c7; }

.city__name::before {
  content: "";
  position: absolute;
  width: 11px;
  height: 16px;
  left: -20px;
  background: url("../img/icon-geo.png") 50% 50% no-repeat; }

.city__name:hover {
  opacity: 0.7; }

.city__name:focus {
  opacity: 0.7; }

.city__button {
  width: 10px;
  height: 6px;
  cursor: pointer;
  background: url("../img/icon-corner.png") 50% 50% no-repeat; }

.city__button:hover {
  opacity: 0.7; }

.city__button:focus {
  opacity: 0.7; }

.phone {
  margin-top: 8px;
