.user-block__cart-link {
  font-family: "Proxima Nova Condensed", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 600;
  color: #1f1f1f;
  text-decoration: none;
  border-bottom: 1px dashed #1f1f1f;
  position: relative;
  margin-right: 15px; }

.user-block__cart-link::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 15px;
  top: 3px;
  left: -25px;
  background: url("../img/icon-cart.png") 50% 50% no-repeat; }

.user-block__cart-link:hover {
  opacity: 0.7; }

.user-block__cart-link:focus {
  opacity: 0.7; }

.user-block__cart-counter {
  font-family: "PT Serif", sans-serif;
