.logo {
  margin-right: 95px;
  margin-bottom: 10px; }

.logo__image {
  display: block;
  width: 157px;
  height: 38px; }

.banner {
  background: url("../img/image-big-man.jpg") 50% 100% no-repeat, url("../img/image-big.jpg") 50% 100% repeat, #c9a57a;
  margin: 0 auto;
  padding-top: 138px;
  padding-bottom: 145px;
  display: flex;
  justify-content: center; }

.banner__inner {
  margin-left: -29%;
