  text-align: center;
  position: relative; }

.banner__inner::before {
  content: "";
  position: absolute;
  top: -28px;
  left: 50%;
  margin-left: -80px;
  width: 160px;
  height: 3px;
  background: #ffffff; }

.banner__inner::after {
  content: "";
